@import "~antd/dist/antd.less";

@color-primary: #531dab;
@color-soft: #9254de;
@color-tough: #612500;
@color-minor: #8c8c8c;
@color-basic-info: #595959;
@color-gz: #237804;
@color-normal: #8c8c8c;
@color-shensha12: #90983c;
@color-liunian12: #813359;
@color-taohua: #c41d7f;
@color-lucun: #d4380d;
@color-tianma: #1890ff;
@color-jieshen: #237804;
@color-xianyun: #fff;
@color-dark: #121212;
@color-stage: #1890ff;
@color-yearly: #876817;
@color-monthly: #67a8a5;
@color-daily: #999;

@color-sh-lu: @color-lucun;
@color-sh-quan: #2f54eb;
@color-sh-ke: #237804;
@color-sh-ji: #120338;

@color-loc4: #d9f7be;
@color-loc3: lighten(@color-loc4, 8%);
@container-bg-color: #00152912;
@palace-bg-color: #f2eff5;
@palace-focused-color: #e8ebf1;
@palace-gap: 5px;
@font-size-big: 14px;
@font-size-middle: 13px;
@font-size-small: 12px;

body {
  background-color: #efefef;
  user-select: none;
}

.palace-container {
  display: grid;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  grid-gap: @palace-gap;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-areas:
    "g3 g4 g5 g6"
    "g2 ct ct g7"
    "g1 ct ct g8"
    "g0 g11 g10 g9";

  .palace {
    background-color: @palace-bg-color;
    border: 1px solid @container-bg-color;
    display: grid;
    padding: @palace-gap;
    text-transform: capitalize;
    grid-template-rows: auto auto 22px 30px 20px;
    grid-template-columns: 0.8fr 1.4fr 0.8fr;
    grid-template-areas:
      "pri  sec other"
      "xyxy  sec other"
      "fate  fate fate"
      "ss12 xy ln12"
      "name dname gz";
    transition: all 0.25s ease-in-out;
    grid-auto-flow: column;
    &-star {
      display: flex;
      align-items: baseline;
      white-space: nowrap;
      &-bright {
        font-weight: normal;
        color: @color-minor;
      }
    }
    &:hover,
    &-focused {
      background-color: @palace-focused-color;
    }
    &-soft {
      color: @color-soft;
    }
    &-tough {
      color: @color-tough;
    }
    &-jieshen {
      color: @color-jieshen;
    }
    &-taohua {
      color: @color-taohua;
    }
    &-lucun {
      color: @color-lucun;
    }
    &-tianma {
      color: @color-tianma;
    }
    &-empty {
      grid-template-rows: auto;
      grid-template-columns: auto;
      grid-template-areas: unset;
      &-name {
        font-size: 3em;
        color: #8c8c8c29;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
    &-0 {
      grid-area: g0;
    }
    &-1 {
      grid-area: g1;
    }
    &-2 {
      grid-area: g2;
    }
    &-3 {
      grid-area: g3;
    }
    &-4 {
      grid-area: g4;
    }
    &-5 {
      grid-area: g5;
    }
    &-6 {
      grid-area: g6;
    }
    &-7 {
      grid-area: g7;
    }
    &-8 {
      grid-area: g8;
    }
    &-9 {
      grid-area: g9;
    }
    &-10 {
      grid-area: g10;
    }
    &-11 {
      grid-area: g11;
    }
    &-ct {
      position: relative;
      grid-area: ct;
      display: block;
      padding: 15px 5px;
      background-color: darken(@palace-bg-color, 1.5) !important;

      h3 {
        text-align: center;
      }
    }
    &-loc4 {
      background-color: @color-loc4;
    }
    &-loc3 {
      background-color: @color-loc3;
    }
    &-name {
      grid-area: name;
      font-size: @font-size-big;
      color: @color-primary;
      white-space: nowrap;
      cursor: pointer;
      font-weight: bold;
      width: 30px;
    }
    &-other {
      grid-area: other;
      justify-self: flex-end;
      color: @color-minor;
      text-align: right;
      display: inline-flex;
      font-size: @font-size-small;
      > div {
        margin-left: @palace-gap;
      }
    }
    &-fate {
      grid-area: fate;
      justify-self: center;
      align-self: flex-end;
    }
    &-fate-item {
      display: inline-block;
      padding: 0 3px;
      margin: 1px;
      background: @color-primary;
      color: @color-xianyun;
      cursor: pointer;
      border-radius: 5px;
      font-size: @font-size-small;
      &.active {
        &-stage {
          background-color: @color-stage;
        }
        &-yearly {
          background-color: @color-yearly;
        }
        &-monthly {
          background-color: @color-monthly;
        }
        &-daily {
          background-color: @color-daily;
        }
      }
    }
    &-fate-star {
      grid-area: xyxy;
      font-size: @font-size-small;
      white-space: nowrap;
      align-self: flex-end;
      &-item {
        white-space: nowrap;
        span {
          margin-right: @palace-gap;
        }
      }
    }
    &-xy {
      grid-area: xy;
      justify-self: center;
      text-align: center;
      font-size: 12px;
      line-height: 16px;
      white-space: nowrap;
    }
    &-stages {
      font-weight: bold;
    }
    &-ss12 {
      grid-area: ss12;
      align-self: flex-end;
      color: @color-shensha12;
      width: 30px;
      font-size: @font-size-small;
      line-height: 16px;
    }
    &-ln12 {
      grid-area: ln12;
      justify-self: flex-end;
      align-self: flex-end;
      text-align: right;
      color: @color-liunian12;
      font-size: @font-size-small;
      line-height: 16px;
    }
    &-gz {
      grid-area: gz;
      justify-self: flex-end;
      align-self: flex-end;
      font-size: @font-size-big;
      color: @color-gz;
      cursor: pointer;
      text-align: right;
      white-space: nowrap;
      transition: all 0.25s ease-in-out;
      font-weight: bold;
      &:hover {
        color: darken(@color-gz, 10%);
      }
    }
    &-pri {
      grid-area: pri;
      font-size: @font-size-middle;
      color: @color-primary;
      white-space: nowrap;
      font-weight: bold;
    }
    &-sec {
      grid-area: sec;
      justify-self: center;
      font-size: @font-size-middle;
      white-space: nowrap;
      font-size: @font-size-middle;
    }
    &-fate-palace-name {
      align-self: flex-end;
      justify-self: center;
      grid-area: dname;
      font-size: @font-size-small;
      line-height: 16px;
      display: flex;
      span {
        display: inline-block;
        margin: 0 1px;
      }
      .stage {
        color: @color-stage;
      }
      .yearly {
        color: @color-yearly;
      }
      .monthly {
        color: @color-monthly;
      }
      .daily {
        color: @color-daily;
      }
    }
  }
}

.palace-self-mutagen {
  position: relative;
  &::before {
    position: absolute;
    content: " ";
    top: 1px;
    bottom: 0;
    left: -@palace-gap;
    width: @palace-gap - 1px;
    background-color: #000;
    border-radius: 4px 0 0 4px;
  }
  &-0 {
    &::before {
      background-color: @color-sh-lu;
    }
  }
  &-1 {
    &::before {
      background-color: @color-sh-quan;
    }
  }
  &-2 {
    &::before {
      background-color: @color-sh-ke;
    }
  }
  &-3 {
    &::before {
      background-color: @color-sh-ji;
    }
  }
}

.palace-dynamical-mutagen {
  position: relative;
  &::after {
    position: absolute;
    content: " ";
    bottom: 0;
    left: 0;
    width: 27px;
  }
  &-0 {
    color: @color-sh-lu !important;
    &::after {
      border-bottom: 2px solid @color-sh-lu;
    }
  }
  &-1 {
    color: @color-sh-quan !important;
    &::after {
      border-bottom: 2px solid @color-sh-quan;
    }
  }
  &-2 {
    color: @color-sh-ke !important;
    &::after {
      border-bottom: 2px solid @color-sh-ke;
    }
  }
  &-3 {
    color: @color-sh-ji !important;
    &::after {
      border-bottom: 2px solid @color-sh-ji;
    }
  }
}

.base-info {
  font-size: 14px;
  color: #595959;
  line-height: 25px;
  display: grid;
  grid-template-columns: auto;
  border-bottom: 1px solid @container-bg-color;
  padding: 5px 0;
  &.grid-4 {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  label {
    font-weight: normal;
    color: @color-daily;
  }
}

.psa-mutagen {
  font-size: 12px;
  background-color: @color-normal;
  color: @color-xianyun;
  display: inline-block;
  padding: 0 2px;
  border-radius: 30%;
  margin-left: 1px;
  cursor: pointer;

  &-0 {
    background-color: @color-sh-lu;
  }
  &-1 {
    background-color: @color-sh-quan;
  }
  &-2 {
    background-color: @color-sh-ke;
  }
  &-3 {
    background-color: @color-sh-ji;
  }
  &-stage {
    background-color: @color-stage !important;
  }
  &-yearly {
    background-color: @color-yearly !important;
  }
  &-monthly {
    background-color: @color-monthly !important;
  }
  &-daily {
    background-color: @color-daily !important;
  }
  &-palace {
    position: absolute;
    opacity: 0.5;
    margin-top: 1px;
  }
}

#palace-line {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.is-taiji-life-palace {
  background: @color-primary;
  color: #fff;
  padding: 2px;
  border-radius: 5px;
}

.astro-button-container {
  position: absolute;
  right: 5px;
  bottom: 5px;
  display: flex;

  .astro-button-item {
    margin-left: 5px;
    opacity: 0.3;
    transition: all 0.25s ease-in-out;
    &:hover {
      opacity: 0.95;
    }
  }
}

.gender {
  &-male {
    color: #2f54eb;
  }
  &-female {
    color: #d4380d;
  }
}

.saved-astro-drawer {
  .ant-drawer-body {
    padding: 0;
  }
  .saved-astro-list {
    .ant-list-pagination {
      margin: 10px;
    }
    .ant-list-item-meta {
      padding-left: 10px;
    }
    .ant-list-item {
      &.selected {
        background-color: fadeout(@color-soft, 90);
      }
    }
  }
}

